<template>
  <div class="checkout">
    <AppBreadcrum />
    <div class="container">
      <div class="text--center mt-1">
        <app-form
          formTitle="Checkout"
          :formData="formData"
          @input="inputHandler"
        >
          <m-button class="checkout__btn">Continue to Payment</m-button>
        </app-form>
      </div>
    </div>
  </div>
</template>

<script>
import AppForm from "../ui/AppForm";
import AppBreadcrum from "../ui/AppBreadcrum";
import mButton from "../ui/Button";

export default {
  name: "CheckoutScreen",
  data() {
    return {
      formData: [
        {
          label: "First Name",
          placeholder: "Enter your first name",
          type: "text",
        },
        {
          label: "Last Name",
          placeholder: "Enter your last name",
          type: "text",
        },
        {
          label: "Shipping Address",
          placeholder: "Enter your address",
          type: "text",
        },

        {
          label: "Town/City",
          placeholder: "Enter your town/city",
          type: "text",
        },

        {
          label: "Phone Number",
          placeholder: "Enter your phone number",
          type: "tel",
        },
      ],

      checkoutForm: {},
    };
  },
  components: {
    AppForm,
    AppBreadcrum,
    mButton,
  },

  methods: {
    inputHandler(e) {
      this.checkoutForm[e.name] = e.value;
      localStorage.setItem("checkoutForm", JSON.stringify(this.checkoutForm));
    },
  },
};
</script>

<style scoped>
.checkout__btn {
  background-color: #333;
  font-weight: 700;
  color: #fff;
  letter-spacing: 0.05rem;
  font-size: 1.1rem;
}
</style>